<template>
  <div class="moves-record">
    <div class="title">Запись партии</div>
    <div class="table">
      <div v-for="(move, i) in moves" :key="i" class="row">
        <div class="cell">{{ i+1 }}.</div>
        <div class="cell">{{ move[0] }}</div>
        <div class="cell">{{ move[1] }}</div>
      </div>
    </div>
    <div class="copy">
      <button class="copy-button" type="button" @click="onCopy">
        <CopyIcon /> Копировать
      </button>
    </div>
  </div>
</template>

<script>
import Types from '../store/modules/chessboard/types'
import CopyIcon from './CopyIcon'

export default {
  name: "MovesRecord",
  components: {
    CopyIcon
  },
  computed: {
    moves() {
      const moves = this.$store.getters[Types.getters.GET_MOVES]();
      const result = [];
      while (moves.length)
        result.push(moves.splice(0, 2));
      return result;
    }
  },
  methods: {
    onCopy() {
      const range = document.createRange();
      const table = this.$el.querySelector('.table');
      range.selectNode(table);
      window.getSelection().removeAllRanges(); // clear current selection
      window.getSelection().addRange(range); // to select text
      document.execCommand("copy");
      window.getSelection().removeAllRanges();// to deselect
    }
  }
}
</script>

